/* Small devices (tablets, 768px and up) */
@media (min-width: @screen-sm-min) {

html {
  font-size: 90%;
}

.container_theme {
  width: 750px;
  margin: auto;
}

//
// Оглавление
// --------------------------------------------------
// 1.Header



//
// 1.Header
// --------------------------------------------------
.header_wrap {
  height: 165px;
  padding-top: 10px;
  box-shadow: 0 0 5px rgba(0, 0, 0, 0.5);
}
.header_contact {
  float: right;
  text-align: left;
}
.header_logo > img {
  max-height: 100%;
}


//
// 2.Navbar
// --------------------------------------------------
.navbar {
  min-height: auto;
}
.navbar-theme {
  background: transparent;
  border-color: transparent;
  margin-top: -50px;
  margin-bottom: 0;

  .container_theme {
    padding: 0;
  }
  .navbar-nav {
    float: none;
    
    > li > a {
      padding: 10px 15px;
      background-color: transparent;

      &:hover,
      &:focus {
        color: @navbar-inverse-link-hover-color;
        background-color: transparent;
      }
    }
    > .active > a {
      &,
      &:hover,
      &:focus {
        color: @navbar-inverse-link-active-color;
        background-color: transparent;
      }
    }

  }
  .navbar-collapse {
    padding: 0;
  }
}



// End  
}