@font-face {
  font-family: 'Century Gothic'; 
  src: url('../fonts/century_gothic.eot'); 
}  
//font-family: 'PT Sans', sans-serif;
@import url(http://fonts.googleapis.com/css?family=PT+Sans:400,700&subset=latin,cyrillic);
//font-family: 'Open Sans', sans-serif;
@import url(http://fonts.googleapis.com/css?family=Open+Sans&subset=latin,cyrillic);
//
// Load core variables and mixins
// --------------------------------------------------

@import "variables.less";
@import "mixins.less";

html {
  font-size: 80%;
}
.container_theme {
  padding: 0 15px;
}


//
// Оглавление
// --------------------------------------------------
// 1.Header
// 2.Navbar


//
// 1.Header
// --------------------------------------------------
.header_wrap {
  background: url('../img/skin.png') repeat-x left bottom;
  color: #eaeaea;
  font-family: 'PT Sans', sans-serif;
  padding: 5px 0 10px;
}
.header_logo > img {
  max-height: 65px;
  .center-block();
}
.header_contact {
  text-align: center;
  >span {
    display: block;
  }
  .tel {
    font-size: 2.25rem; //36px
    font-family: 'Century Gothic', sans-serif;
  }
  .mode {
    font-size: 1.05rem; //17px
    vertical-align: top;
    line-height: 1.10rem;
  }
  .indicator {
    background: url('../img/csg-549baf48a8121.png') no-repeat top left;
    background-position: 0 -27px; width: 21px; height: 22px;
    display: inline-block;
    vertical-align: top;

    &.off {
      background-position: 0 0; width: 21px; height: 22px;
    }
  }
  .desc {
    font-size: 0.7rem; //11px
  }
}



//
// 2.Navbar
// --------------------------------------------------
.navbar-theme {
  background-color: @navbar-inverse-bg;
  border-color: @navbar-inverse-border;
  font-family: 'PT Sans', sans-serif;
  font-weight: bold;
  font-size: 1.5rem;


  .navbar-brand {
    color: @navbar-inverse-brand-color;
    &:hover,
    &:focus {
      color: @navbar-inverse-brand-hover-color;
      background-color: @navbar-inverse-brand-hover-bg;
    }
  }

  .navbar-text {
    color: @navbar-inverse-color;
  }

  .navbar-nav {
    > li > a {
      color: @navbar-inverse-link-color;

      &:hover,
      &:focus {
        color: @navbar-inverse-link-hover-color;
        background-color: @navbar-inverse-link-hover-bg;
      }
    }
    > .active > a {
      &,
      &:hover,
      &:focus {
        color: @navbar-inverse-link-active-color;
        background-color: @navbar-inverse-link-active-bg;
      }
    }
    > .disabled > a {
      &,
      &:hover,
      &:focus {
        color: @navbar-inverse-link-disabled-color;
        background-color: @navbar-inverse-link-disabled-bg;
      }
    }
  }

  // Darken the responsive nav toggle
  .navbar-toggle {
    border-color: @navbar-inverse-toggle-border-color;
    &:hover,
    &:focus {
      background-color: @navbar-inverse-toggle-hover-bg;
    }
    .icon-bar {
      background-color: @navbar-inverse-toggle-icon-bar-bg;
    }
  }

  .navbar-collapse,
  .navbar-form {
    border-color: darken(@navbar-inverse-bg, 7%);
  }

  // Dropdowns
  .navbar-nav {
    > .open > a {
      &,
      &:hover,
      &:focus {
        background-color: @navbar-inverse-link-active-bg;
        color: @navbar-inverse-link-active-color;
      }
    }

    @media (max-width: @grid-float-breakpoint-max) {
      // Dropdowns get custom display
      .open .dropdown-menu {
        > .dropdown-header {
          border-color: @navbar-inverse-border;
        }
        .divider {
          background-color: @navbar-inverse-border;
        }
        > li > a {
          color: @navbar-inverse-link-color;
          &:hover,
          &:focus {
            color: @navbar-inverse-link-hover-color;
            background-color: @navbar-inverse-link-hover-bg;
          }
        }
        > .active > a {
          &,
          &:hover,
          &:focus {
            color: @navbar-inverse-link-active-color;
            background-color: @navbar-inverse-link-active-bg;
          }
        }
        > .disabled > a {
          &,
          &:hover,
          &:focus {
            color: @navbar-inverse-link-disabled-color;
            background-color: @navbar-inverse-link-disabled-bg;
          }
        }
      }
    }
  }

  .navbar-link {
    color: @navbar-inverse-link-color;
    &:hover {
      color: @navbar-inverse-link-hover-color;
    }
  }

  .btn-link {
    color: @navbar-inverse-link-color;
    &:hover,
    &:focus {
      color: @navbar-inverse-link-hover-color;
    }
    &[disabled],
    fieldset[disabled] & {
      &:hover,
      &:focus {
        color: @navbar-inverse-link-disabled-color;
      }
    }
  }
}



